*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    
}

body{
    font-family: 'Karla', sans-serif; 
}

h1,h3{
box-shadow: 0 0 2px;
margin-top: 0;
font-family: 'Karla', sans-serif;
font-size: 2.5rem;
font-weight: 700;
color: #333; 
text-align: center;
margin-bottom: 20px;
text-transform: capitalize; 
line-height: 1.2;
cursor: pointer; 
transition: background-color 0.3s ease; 
padding: 10px 20px; 
}
h2{
    font-family: 'Karla', sans-serif;
    font-size: 2.5rem;
    font-weight: 700;
    color: #FFD54F; 
    text-align: center;
    margin-bottom: 20px;
    text-transform: capitalize; 
    line-height: 1.2;
    cursor: pointer; 
    transition: background-color 0.3s ease; 
    padding: 10px 20px;    
}
    
 h1:hover {
        background-color: #f0f0f0; 
 }
 h3{
    box-shadow: 0 0 2px;
    margin-top: 0;
    font-family: 'Karla', sans-serif;
    font-size: 2rem;
    font-weight: 700;
    color: #7E5BEF; 
    text-align: center;
    text-transform: capitalize; 
    line-height: 1.2;
    cursor: pointer; 
    transition: background-color 0.3s ease; 
    padding: 10px 20px; 
    }
.like-icon{
    color: orange;
    font-size: 1.5rem;
}



.content-container{
    position: absolute;
    width: 50%;
    height: 100%;
    margin-top: 140px;
    margin-left: -300px;
    
    
}
.message{
    text-align: center;
    position: absolute;
    top: 70%;
    left: 40%;
    color: white;
    font-weight: bold;
    font-size: 2rem;   
}
.message2{
    text-align: center;
    position: absolute;
    top: 80%;
    left:37%;
    color: white;
    font-weight: bold;
    font-size: 2rem;   
}
.post {
    text-align: center;
    line-height: 1.2;
    width: 100%; 
    padding: 30px;
    margin-top: 20px;
    gap: 0px;
    background-color: #E0F7fA;
    color: #37474F;
    border-radius: 15px;
    box-shadow: 0 0 5px;
}  

.post p{
 font-size: 1.2rem;
 line-height: 1.2;

 font-weight: bold;
 color:rgb(26, 24, 24)
}

label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: bold;
}

input {
    padding: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.img-container img {
    display: block;
    width: 100%; 
    height: auto; 
    margin: 10px auto; 
    border-radius: 15px;
    box-shadow: 0 0 5px;
}


.sign-btn{
    border: none;
    padding: 10px;
    margin-top: 20px;
    margin-left: 50px;
    margin-right: auto;
    border-radius: 15px;
    color: lightgreen;
    width: 100px;
    background-image: linear-gradient(black, gray);
}



#sign-btn:hover{
    background-color: #0056b3;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    transform: scale(1.1);
    background-color: white;
    color: #dc3545;
}

.error{
    color: red;
    font-weight: bold;
    font-size: 1.2rem;
    margin-top: 6px;


}
.right-bar-container {
    height: 100vh;
    width: 250px;
    background-color: #4C2C82; 
    display: flex;
    flex-direction: column;
    gap: 50px;
    padding: 20px;
    color: white;
    position: fixed;
    top: 0;
    right: 0;
    box-shadow: 0 0 10px black;
    z-index: 1;
    transform: translateX(250px);
    transition: transform 0.3s ease;
    overflow-y: auto;
}
.sidebarContainer {
    height: 100vh;
    width: 250px;
    background-color: #4C2C82; 
    display: flex;
    flex-direction: column;
    padding: 20px;
    color: white;
    position: fixed;
    top: 0;
    left: 0;
    box-shadow: 0 0 10px black;
    z-index: 1;
    transform: translateX(-250px);
    transition: transform 0.3s ease;
    overflow-y: auto;
  }


.right-bar-container.show{
    transform: translateX(0); 
}
.sidebarContainer.show{
transform: translateX(0); 
}


.hamburger-right-sidebar{
    display: block; 
    position: fixed;
    top: 2%;
    right: 3%;
    z-index: 2;
    font-size: 1.5rem;      
    cursor: pointer;
    color: whitesmoke;
}
.hamburger-right-sidebar:hover{
    color: orangered;
    transform: scale(1.1);
}
.hamburger-sidebar{
    display: block;
    position: fixed;
    top: 2%;
    left: 3%;
    z-index: 2;
    font-size: 1.5rem;      
    cursor: pointer; 
    color: whitesmoke;
}
.hamburger-sidebar:hover{
    color: orangered;
    transform: scale(1.1);
}
